/* src/pages/Home.css */
.home {
  text-align: center;
 
}

.hero {
  position: relative;
  height: 600px;
  /* Adjust height as needed */
   overflow: hidden;
margin-bottom: 50px;
background-color: black;

} 

 .hero img {
  width:100%;
  height:100%;
  object-fit: cover;
  
} 

 .hero-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
} 

.shop-now {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #7c3616; 
  /* Coffee color */
   color: white;
  border: none;
  cursor: pointer;
}

 .featured-products {
  padding: 20px;
  margin: 10px;
}

.product-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
} 

.product {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 30px;
  padding: 10px; 
  /* width: 200px;
  /* Adjust width as needed */
 }

.product img {
  width: 100%;
  height: auto;
} 
/* Navbar */
.navbar {
  background-color:#7c360a;
  padding:0px;
  box-shadow: 0px 2px 2px #6F4E37;
