     /* Container styling */
         .about-container {
         max-width: 1000px;;
           margin: 0 auto;
           padding: 20px;
           font-family: 'Arial', sans-serif;
           line-height: 1.6;
           color: #333;
           
         }
    
         .about-heading {
           font-size: 2.5rem;
           margin-bottom: 20px;
           color: #7c360a;
           /* Coffee brown */
           text-align: center;
         }
    
         .about-subheading {
           font-size: 1.8rem;
           margin: 20px 0 10px;
           color: #7c360a;
           /* Coffee brown */
         }
    
         .about-text {
           font-size: 1rem;
           margin-bottom: 15px;
         }
        .about-list {
          margin: 0;
          /* Removes default margin */
          padding: 0;
          /* Removes default padding */
          list-style-type: none;
          /* Remove default bullet points */
          display: flex;
          /* Use flexbox for the list */
          flex-direction: column;
          /* Stack items vertically */
          align-items: center;
          /* Center items horizontally */
        }
    
        .about-list li {
          display: flex;
          /* Use flexbox for alignment */
          align-items: center;
          /* Center items vertically */
          margin: 5px 0;
          /* Adds vertical spacing between each item */
          padding-left: 20px;
          /* Space for custom bullet */
          position: relative;
          /* Position for custom bullet */
        }
    
        .about-list li::before {
          content: '';
          /* Create a custom bullet */
          position: absolute;
          /* Position it absolutely */
          left: 0;
          /* Align it to the left */
          top: 50%;
          /* Center it vertically */
          transform: translateY(-50%);
          /* Adjust for perfect centering */
          width: 8px;
          /* Width of the bullet */
          height: 8px;
          /* Height of the bullet */
          background-color: black;
          /* Color of the bullet */
          border-radius: 50%;
          /* Make it circular */
        }